  padding-top: 5px;
  padding-bottom: 5px;
  padding-left: 10px;
  padding-right: 10px;
 
  border-radius: 4px;
  text-shadow: 0 1px 1px #740001;
}

button:disabled,
button[disabled]{
  color: #816767;
  background-color: #8C8265;
  font-family: Staatliches;
  border: none;
  text-shadow: none;
  border-radius: 4px;
  
}

